class AddTodo extends React.Component {
    state = {
        title: ''
    }

    onChange = (e) => this.setState ({ title: e.target.value});

    onSubmit = (e) => {
        e.preventDefault();
        this.props.addTodo(this.state.title);
        this.setState({ title: ''});
    }

    render() {
      return (
        <form  onSubmit={this.onSubmit} style={{width: '100%', display: 'flex'}}>
           <p style={{ float: 'left'}} >To complete a task click the check box</p>

            <input 
                type="text" 
                name="title"
                placeholder="Add Todo"
                style={{ float: 'right', marginTop: '10px', width: '190px', padding: '5px',marginLeft: '17px', marginBottom: '10px',}} 
                value={this.state.title}
                onChange={this.onChange}
                />
            <input
                type="submit"
                value="ADD"
                style={{ cursor: 'pointer', margin: '10px', width: '70px', borderRadius: '50px', backgroundColor: '#333', color: 'white'}} 
                />

        </form>
        
      );
    }
  }
  
  export default AddTodo;
  